body {
  margin: 0;
  padding: 0;
  background-color: #0954bc;
  font-family: Arial, Helvetica, sans-serif;
}

header {
  width: 100%;
  height: 52px;
  background-color: #0954bc;
}

.titulo {
  text-align: center;
  font-style: italic;
  font-size: 25px;
  color: azure;
}

.subtitulo,
.ion-text-center {
  text-align: center;
  font-size: 20px;
  padding: 0 10px 0 10px;
}

.features {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0 30px;
  font-size: 18px;

  .element1,
  .element2,
  .element3,
  .element4,
  .element5,
  .element6 {
    width: calc(30% - 10px);
    margin-bottom: 20px;
    box-sizing: border-box;
  }

  @media (max-width: 768px) {
    .element1,
    .element2,
    .element3,
    .element4,
    .element5
     {
      width: calc(50% - 20px);
    }
    .element6 {
      width: 50%;
    }
  }
}

#qrCode {
  display: none;

  img {
    max-width: 30%;
    height: auto;
    margin: 0 auto;
    display: block;
  }
}

#downloadButton {
  display: none;
  font-size: 24px;
  background-color: yellow;
  color: #000;
  padding: 15px 30px;
  border: none;
  margin: 0 auto;
  margin-top: 20px;
}

#content-space {
  height: 80px; /* Ajusta seg√∫n sea necesario */
}

footer {
  text-align: center;
  background-color: #003785;
  color: white;
  padding: 10px;
  margin: 0;
  box-sizing: border-box;
  border-top: 2px solid #0954bc;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60px;
}